import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';

import { MenuPage } from './menu.page';

const routes: Routes = [
  {
    path: 'menu',
    component: MenuPage,
    children: [
      {
        path: 'home',
        loadChildren: () => import('../home/home.module').then( m => m.HomePageModule)
      },
      {
        path: 'login',
        loadChildren: () => import('../login/login.module').then( m => m.LoginPageModule)
      },
      {
        path: 'registration',
        loadChildren: () => import('../registration/registration.module').then( m => m.RegistrationPageModule)
      },
      {
        path: 'forgot-phrase',
        loadChildren: () => import('../forgot-phrase/forgot-phrase.module').then( m => m.ForgotPhrasePageModule)
      },
      {
        path: 'task-details',
        loadChildren: () => import('../task-details/task-details.module').then( m => m.TaskDetailsPageModule)
      },
      {
        path: 'task-details/:id',
        loadChildren: () => import('../task-details/task-details.module').then( m => m.TaskDetailsPageModule)
      },
      {
        path: 'task-list',
        loadChildren: () => import('../task-list/task-list.module').then( m => m.TaskListPageModule)
      },
    ]
  },
  {
    path: '',
    redirectTo: '/menu/home',
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes)
  ],
  declarations: [MenuPage]
})
export class MenuPageModule { }
